<template>
  <div>
    <new-banner :item="banner"></new-banner>
    <div class="linefal">
      <div class="line"></div>
      <div class="linename">
        <van-icon class="diamondos" name="diamond-o" />
        <span>{{this.$route.query.titles}}</span>
      </div>
      <div class="line"></div>
    </div>
    <quality :item="list"></quality>
    <!-- <recommendson
      v-for="listson in list"
      :key="listson.id"
      :item="list"
    ></recommendson> -->
  </div>
</template>
<script>
import NewBanner from "@/components/NewBanner";
// import Recommendson from "@/components/Recommendson";
import Quality from '@/components/Quality'
export default {
  data() {
    return {
      banner: [],
      list: [],
    };
  },
  components: {
    // Recommendson,
    NewBanner,
    Quality
  },
  created() {
    this.$axios.get("api/groom/list/"+`${this.$route.query.userId}`+"?page=1&limit=20").then((d) => {
      this.banner = d.data.data.banner;
      this.list = d.data.data.list;
      console.log(d);
    });
  },
};
</script>
