.linefal {
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #f5f5f5;
  .linename {
    height: 60px;
    display: flex;
    align-items: center;
    justify-content: center;
    span {
      font-size: 16px;
    }
    .diamondos {
      font-size: 20px;
      margin-right: 6.5px;
    }
  }
